.rated-details::after {
  content: "";
  top: 100%;
  left: 0;
  position: absolute;
  height: 4px;
  width: 100%;
  background-color: #820000;
  opacity: 0;
  visibility: hidden;
}
.rateblog:hover .rated-details::after {
  opacity: 1;
  visibility: visible;
}
.rated-icons {
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  width: 100%;
  padding: 20px;
  bottom: 0;
  left: 0;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.rateblog:hover .rated-icons {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
